import React, { useEffect, useState } from 'react'
// import Star from "../../assets/images/cuenta/starfull.svg";
// import StarEmpty from "../../assets/images/cuenta/starborder.svg";
import { AiFillStar, AiOutlineStar } from "react-icons/ai";

const StarRating = ({score}) => {
    const [stars, setStars] = useState([0, 0, 0, 0, 0]);

    useEffect(() => {
        const countingStars = stars.map((el, i) => {
            if (i < score) {
                return 1; // filled
            } else {
                return 0; // empty
            }
        });
        setStars(() => countingStars);
        // eslint-disable-next-line
    }, [score]);
    return (
    <>
        <div>

            <div className="flex">
                {stars.map((el, i) =>
                    el === 1 ? <AiFillStar className="text-amber-300 text-2xl" key={i} /> : <AiOutlineStar className="text-amber-300 text-2xl" key={i} />
                )}
            </div>
        </div>
    </>
    )
}

export default StarRating