import React from 'react'
import Perfil from "../../assets/images/cuenta/perfil2.png";
import Icon from "../../assets/images/icon-loc.svg";
import Star from "../../assets/images/cuenta/starfull.svg";
import StarEmpty from "../../assets/images/cuenta/starborder.svg";
import Tabs from './Tabs';


const Cuenta = () => {
  return (
    <>
        <div className="container mx-auto mt-36 md:mt-48">
            <div className="w-10/12 md:w-8/12  mx-auto  card-op my-8 shadow-xl rounded-lg bg-white shadow-gray-300 py-4 ">
                <div className="flex justify-center flex-col items-center">
                    <img className="w-36" src={Perfil} alt="" />
                    <h2 className="text-center text-body my-4 font-bold text-lg">Jane Doe</h2>
                    <div className="flex">
                        <img className="mx-3" src={Icon} alt="" />
                        <p>Santiago, Región Metropolitana</p>
                    </div>
                    <div className="peticiones flex flex-wrap w-10/12 md:w-6/12 lg:w-4/12 text-center my-4">

                        <div className="peticion  w-6/12 p-3 border-r border-solid border-orange-600">
                            <p className="text-body">
                                Peticiones <br /> de cotización
                            </p>
                            <p className="text-body font-bold text-lg">
                                6
                            </p>
                        </div>

                        <div className="peticion  w-6/12 p-3">
                            <p className="text-body">
                            Cotizaciones <br /> concretadas
                            </p>
                            <p className="text-body font-bold text-lg">
                                5
                            </p>
                        </div>
                    </div>
                </div>
                <div className="opiniones w-10/12 lg:w-8/12 mx-auto my-8 flex justify-between flex-wrap px-2">

                    <div>
                        <p className="text-lg text-body font-bold">
                            Opiniones sobre comprador
                        </p>
                        <div className="stars my-4 flex">
                            <img className="mx-1 w-6" src={Star} alt="" />
                            <img className="mx-1 w-6" src={Star} alt="" />
                            <img className="mx-1 w-6" src={Star} alt="" />
                            <img className="mx-1 w-6" src={Star} alt="" />
                            <img className="mx-1 w-6" src={StarEmpty} alt="" />
                        </div>
                    </div>

                    <a className="text-body font-bold underline underline-offset-[1em] my-4" href="www.google.com">Escribir opinión</a>

                </div>
                <Tabs />
            </div>
        </div>
    </>
  )
}

export default Cuenta