import React, {useState} from 'react'
import StarRating from './StarRating';


const Tabs = () => {

    const reviews= [
        {nombre: "The Cake maker shop", reseña: "Lorem ipsum dolor sit amet, consectetur adipiscing elit. Molestie aliquam diam vivamus morbi velit.  Sagittis pulvinar dapibus egestas dignissim mollis in.", rating: 4},
        {nombre: "The Weed maker shop", reseña: "Lorem ipsum dolor sit amet, consectetur adipiscing elit. Molestie aliquam diam vivamus morbi velit.  Sagittis pulvinar dapibus egestas dignissim mollis in.", rating: 2},
        {nombre: "The Make maker shop", reseña: "Lorem ipsum dolor sit amet, consectetur adipiscing elit. Molestie aliquam diam vivamus morbi velit.  Sagittis pulvinar dapibus egestas dignissim mollis in.", rating: 1},
    ]
    
    const [reviewsFiltrada, setReviewsFiltrada] = useState(reviews);

    const [toggleState, setToggleState] = useState(1);

    const toggleTab = (index) => {
        setToggleState(index)
        if(index === 3){
            const menoresQue3 = reviews.filter( item =>(item.rating < 3)); 
            // console.log(menoresQue3)  
            setReviewsFiltrada(menoresQue3)
        }else if(index === 2){
            const mayoresQue3 = reviews.filter( item =>(item.rating >= 3)); 
            // console.log(mayoresQue3)  
            setReviewsFiltrada(mayoresQue3)  
        }else{
            setReviewsFiltrada(reviews)
        }
    }

    const tab1 = "w-4/12 absolute border-b-4 border-cyan-800 bottom-0 left-0"
    const tab2 = "w-4/12 absolute border-b-4 border-cyan-800 bottom-0 left-[33.3%]"
    const tab3 = "w-4/12 absolute border-b-4 border-cyan-800 bottom-0 left-[66.6%]"


  return (
    <>
    <div className="px-6">
        <div className=" flex flex-wrap relative">
            <div className={toggleState === 1 ? tab1 :  toggleState === 2  ?  tab2  : tab3}></div>
            {/* <div className={`tab${toggleState}`}></div> */}
            <div className="w-4/12 cursor-pointer" onClick={ () => toggleTab(1)}>
                <h3 className="text-body text-center border-b border-cyan-800 border-solid py-4">
                    <span className={toggleState === 1 ? "font-bold" :  "font-normal"}>
                        Todas
                    </span>
                </h3>
            </div>
            <div className="w-4/12 cursor-pointer" onClick={ () => toggleTab(2)}>
                <h3 className="text-body text-center border-b border-cyan-800 border-solid py-4">
                    <span className={toggleState === 2 ? "font-bold" :  "font-normal"}>
                        Positivas
                    </span>
                </h3>
            </div>
            <div className="w-4/12 cursor-pointer" onClick={ () => toggleTab(3)}>
                <h3 className="text-body text-center border-b border-cyan-800 border-solid py-4">
                    <span className={toggleState === 3 ? "font-bold" :  "font-normal"}>
                        Negativas
                    </span>
                </h3>
            </div>
            
        </div>
        <div className="reviews px-8 py-6">

        {reviewsFiltrada.map((item, i) => (
            <div key={i} className="review-content py-4 border-b">
                <StarRating score={item.rating} />
                <h3 className="text-body font-bold text-lg my-2">{item.nombre}</h3>
                <p className="text-body">{item.reseña}</p>
            </div>
        ))} 

        </div>
    </div>

    </>
    )
}

export default Tabs